.ecash_Wrap {
  margin: 0 auto;
  width: 1200px;
}

.ecash_header {
  display: flex;
  align-items: center;
  margin-bottom: 40px;
}

.ecash_header a {
  display: block;
  margin-right: auto;
  width: 135px;
  height: 35px;
  background: url(../../dig/images/eCash/logo_ecash.png) no-repeat left center;
}

.ecash_header em {
  display: inline-block;
  width: 18px;
  height: 18px;
  background-position: center;
  background-repeat: no-repeat;
}

.ecash_header em.ico_conv {
  background-image: url(../../dig/images/eCash/ico_convert.png);
}

.ecash_header em.ico_regi {
  background-image: url(../../dig/images/eCash/ico_number_register.png);
}

.ecash_header button + button {
  margin-left: 6px;
}

.ecash_menu {
  position: relative;
  display: flex;
  align-items: center;
  width: 100%;
}

.ecash_menu button {
  flex-grow: 0;
  margin-left: -1px;
  width: 160px;
  height: 44px;
  font-size: 14px;
  color: #767676;
  line-height: 44px;
  box-sizing: border-box;
  border-radius: 6px 6px 0 0;
  border: 1px solid #d5d5d5;
  border-bottom: 1px solid #000;
}

.ecash_menu button em.event {
  display: inline-block;
  width: 49px;
  height: 24px;
  vertical-align: middle;
  background: url(../images/eCash/ico_event.png) no-repeat left center;
  text-indent: -1000px;
  overflow: hidden;
}

.ecash_menu button.current {
  font-weight: 500;
  color: #000;
  border: 1px solid #000;
  border-bottom-color: transparent;
  z-index: 1;
}

.ecash_menu:after {
  position: absolute;
  right: 0;
  bottom: 0;
  width: calc(100% - 318px);
  height: 1px;
  background-color: #000;
  z-index: -1;
  content: "";
}

.ecash_autoCharge_tab {
  display: flex;
  flex-wrap: wrap;
  align-items: center;
  gap: 8px 5px;
  margin-top: 40px;
}

.ecash_autoCharge_tab button {
  padding: 0 16px;
  height: 36px;
  font-size: 13px;
  color: #595959;
  line-height: 100%;
  border-radius: 16px;
  background-color: #f7f7f7;
  cursor: pointer;
}

.ecash_autoCharge_tab button.current {
  font-weight: 500;
  color: #fff;
  background-color: #595959;
}

.ecash_autoCharge_tab p {
  width: 100%;
  font-size: 12px;
  color: #767676;
}

.ecash_autoCharge_tab p:last-of-type {
  display: none;
}

.ecash_autoCharge_cancel {
  position: relative;
  margin-top: 40px;
  padding: 23px;
  box-sizing: border-box;
  border-radius: 16px;
  background-color: #f1f1fc;
}

.ecash_autoCharge_cancel b {
  display: block;
  margin-bottom: 4px;
  padding-left: 24px;
  font-size: 16px;
  color: #000;
  line-height: 24px;
  background: url(../../dig/images/common/ico_info.png) no-repeat left -3px top -1px;
}

.ecash_autoCharge_cancel span {
  display: inline-block;
  padding-left: 24px;
  font-size: 13px;
  color: #767676;
}

.ecash_autoCharge_cancel button {
  position: absolute;
  right: 20px;
  top: 20px;
  width: 15px;
  height: 15px;
  text-indent: -1000px;
  background: url(../../common/images/btn_selected_option_remove.png) no-repeat center;
  overflow: hidden;
  cursor: pointer;
}

.ecash_autoCharge_wrap {
  margin-top: 40px;
}

.ecash_autoCharge_wrap h5 {
  font-size: 20px;
  line-height: 30px;
}

.ecash_autoCharge_wrap > div {
  display: flex;
  align-items: center;
  gap: 0 8px;
  margin-top: 20px;
  padding: 20px;
  font-size: 16px;
  color: #000;
  line-height: 23px;
  border-radius: 16px;
  background-color: #f7f7f7;
}

.ecash_autoCharge_wrap > div + h5 {
  display: none;
}

.ecash_autoCharge_wrap > div:last-of-type {
  display: none;
}

.ecash_autoCharge_wrap > div .form_sel {
  width: 120px;
}

.ecash_autoCharge_set {
  margin-top: 40px;
}

.ecash_autoCharge_set h5 {
  font-size: 20px;
  line-height: 30px;
}

.ecash_autoCharge_set h5 em {
  font-weight: 400;
  font-size: 14px;
  color: #767676;
}

.ecash_autoCharge_set > div {
  margin-top: 20px;
  display: flex;
  flex-direction: column;
  padding: 24px;
  border-radius: 16px;
  background-color: #f7f7f7;
}

.ecash_autoCharge_set > div strong {
  font-size: 20px;
  color: #000;
}

.ecash_autoCharge_set > div p {
  margin-top: 8px;
  font-size: 16px;
  color: #000;
}

.ecash_autoCharge_set > div p .blue {
  color: #474c98;
}

.ecash_autoCharge_set > div p .green {
  color: #3c9a17;
}

.ecash_autoCharge_set > div div {
  display: flex;
  align-items: center;
  justify-content: space-between;
  margin-top: 16px;
  padding: 18px;
  border-radius: 16px;
  background-color: #fff;
}

.ecash_autoCharge_set > div div dl {
  display: flex;
  flex-wrap: wrap;
  align-items: center;
  gap: 8px 0;
}

.ecash_autoCharge_set > div div dl dt {
  width: 86px;
  font-weight: 600;
  color: #000;
}

.ecash_autoCharge_set > div div dl dd {
  width: calc(100% - 86px);
  color: #595959;
}

.ecash_autoCharge_set > div div button {
  min-width: auto;
}

.ecash_autoCharge_set > div div button .text {
  font-weight: 500 !important;
  font-size: 14px !important;
  letter-spacing: normal;
}

.ecash_autoCharge_set > div > span {
  margin-top: 12px;
  font-size: 12px;
  color: #767676;
}

.ecash_content {
  display: flex;
  align-items: flex-start;
  justify-content: space-between;
}

.ecash_content_left {
  width: 788px;
}

.ecash_content_left .banner_top {
  margin-top: 40px;
}

.ecash_content_left .banner_mid {
  margin-top: 40px;
}

.ecash_content_right {
  width: 376px;
}

.ecash_content_price {
  display: flex;
  flex-wrap: wrap;
  gap: 20px 20px;
  margin-top: 40px;
}

.ecash_content_price_item {
  display: flex;
  flex-direction: column;
  position: relative;
  padding: 20px 18px;
  width: 384px;
  box-sizing: border-box;
  border: 1px solid #d5d5d5;
  border-radius: 16px;
  overflow: hidden;
  cursor: pointer;
}

.ecash_content_price_item p {
  padding-left: 30px;
  font-size: 18px;
  color: #000;
}

.ecash_content_price_item p input[type=text] {
  padding: 0 14px;
  width: 130px;
  height: 44px;
  text-align: right;
  vertical-align: baseline;
  border: 1px solid #d5d5d5;
  border-radius: 6px;
  box-sizing: border-box;
}

.ecash_content_price_item p input[type=text]::placeholder {
  font-size: 14px;
  color: #767676;
}

.ecash_content_price_item div {
  display: flex;
  flex-direction: column;
  margin-top: 20px;
  padding: 12px;
  border-radius: 16px;
  box-sizing: border-box;
  background-color: #f7f7f7;
}

.ecash_content_price_item div b {
  margin-bottom: 6px;
  font-size: 14px;
  color: #000;
  line-height: 22px;
}

.ecash_content_price_item div span {
  font-size: 13px;
  color: #595959;
  line-height: 19px;
}

.ecash_content_price_item input[type=radio] {
  position: absolute;
  left: -100px;
  top: -100px;
}

.ecash_content_price_item:before {
  position: absolute;
  left: 18px;
  top: 20px;
  display: inline-block;
  width: 22px;
  height: 22px;
  text-align: center;
  border-radius: 50%;
  box-sizing: border-box;
  background-color: #fff;
  border: 1px solid #ccc;
  transition: border-color 0.2s ease-out, background-color 0.2s ease-out;
  content: "";
}

.ecash_content_price_item:after {
  content: "";
  position: absolute;
  left: 24px;
  top: 26px;
  display: inline-block;
  width: 10px;
  height: 10px;
  text-align: center;
  border-radius: 50%;
  box-sizing: border-box;
  background-color: #ccc;
  transition: background 0.2s ease-out;
}

.ecash_content_price_item.checked {
  border-color: #5055b1;
}

.ecash_content_price_item.checked p {
  color: #474c98;
}

.ecash_content_price_item.checked:before {
  background-color: #5055b1;
  border: 1px solid #5055b1;
  transition: border-color 0.2s ease-out, background-color 0.2s ease-out;
}

.ecash_content_price_item.checked:after {
  background-color: #fff;
  transition: background 0.2s ease-out;
}

.ecash_content_price_item.direct p {
  font-weight: 500;
  font-size: 16px;
  color: #000;
}

.ecash_content_price_item.direct:before {
  top: 30px;
}

.ecash_content_price_item.direct:after {
  top: 36px;
}

.ecash_paymethod {
  position: relative;
  margin-top: 70px;
}

.ecash_paymethod h5 {
  font-size: 19px;
  color: #000;
}

.ecash_paymethod h5 + .tipInfo {
  position: absolute;
  right: 0;
  top: 0;
}

.ecash_paymethod h5 + .tipInfo > span {
  font-weight: 500;
  font-size: 14px;
  color: #000;
}

.ecash_paymethod h5 + .tipInfo .tooltip_wrap .tooltip_inner {
  width: 345px;
}

.ecash_paymethod .payRegister {
  position: absolute;
  right: 0;
  top: 0;
}

.ecash_paymethod .method {
  display: flex;
  align-items: flex-start;
  justify-content: space-between;
  margin-top: 20px;
}

.ecash_paymethod .method button {
  padding-top: 50px;
  width: 110px;
  height: 122px;
  font-weight: 500;
  font-size: 16px;
  color: #767676;
  line-height: 24px;
  box-sizing: border-box;
  border: 1px solid #ccc;
  border-radius: 16px;
  background-position: center 24px;
  background-repeat: no-repeat;
  cursor: pointer;
}

.ecash_paymethod .method button:nth-child(1) {
  background-image: url(../../dig/images/eCash/ico_method_card.png);
}

.ecash_paymethod .method button:nth-child(2) {
  background-image: url(../../dig/images/eCash/ico_method_phone.png);
}

.ecash_paymethod .method button:nth-child(3) {
  background-image: url(../../dig/images/eCash/ico_method_dollar.png);
}

.ecash_paymethod .method button:nth-child(4) {
  background-image: url(../../dig/images/eCash/ico_method_simple_pay.png);
}

.ecash_paymethod .method button:nth-child(5) {
  background-image: url(../../dig/images/eCash/ico_method_money.png);
}

.ecash_paymethod .method button:nth-child(6) {
  background-image: url(../../dig/images/eCash/ico_method_others.png);
}

.ecash_paymethod .method button.current {
  color: #474c98;
  border-color: #5055b1;
}

.ecash_paymethod .method button.current:nth-child(1) {
  background-image: url(../../dig/images/eCash/ico_method_card_on.png);
}

.ecash_paymethod .method button.current:nth-child(2) {
  background-image: url(../../dig/images/eCash/ico_method_phone_on.png);
}

.ecash_paymethod .method button.current:nth-child(3) {
  background-image: url(../../dig/images/eCash/ico_method_dollar_on.png);
}

.ecash_paymethod .method button.current:nth-child(4) {
  background-image: url(../../dig/images/eCash/ico_method_simple_pay_on.png);
}

.ecash_paymethod .method button.current:nth-child(5) {
  background-image: url(../../dig/images/eCash/ico_method_money_on.png);
}

.ecash_paymethod .method button.current:nth-child(6) {
  background-image: url(../../dig/images/eCash/ico_method_others_on.png);
}

.ecash_paymethod_type {
  margin-top: 30px;
  padding-top: 30px;
  border-top: 1px solid #eaeaea;
}

.ecash_paymethod_type .box {
  display: flex;
  flex-wrap: wrap;
  align-items: center;
  justify-content: space-between;
  margin-top: 30px;
  padding: 20px;
  border-radius: 16px;
  background-color: #f7f7f7;
}

.ecash_paymethod_type .box .tit {
  position: relative;
  font-weight: 600;
  font-size: 16px;
  color: #000;
  line-height: 22px;
  cursor: pointer;
}

.ecash_paymethod_type .box .tit:after {
  position: absolute;
  right: -30px;
  top: 0;
  display: inline-block;
  width: 22px;
  height: 22px;
  background: url(../../common/images/arw_tooltip.png) no-repeat center;
  transform: rotate(0deg);
  transition: all ease 0.3s;
  content: "";
}

.ecash_paymethod_type .box .tit.close:after {
  transform: rotate(180deg);
}

.ecash_paymethod_type .box .tip {
  position: relative;
  padding-right: 22px;
  font-weight: 500;
  font-size: 14px;
  color: #000;
  line-height: 20px;
  cursor: pointer;
}

.ecash_paymethod_type .box .tip:after {
  position: absolute;
  right: 0;
  top: 2px;
  display: inline-block;
  width: 16px;
  height: 16px;
  background: url(../../common/images/btn_tooltip_sm.png) no-repeat center;
  content: "";
}

.ecash_paymethod_type .box > div {
  margin-top: 20px;
  padding-top: 20px;
  width: 100%;
  border-top: 1px solid #eaeaea;
}

.ecash_paymethod_type .box > div h6 {
  margin-bottom: 6px;
  font-weight: 500;
  font-size: 13px;
  color: #000;
  line-height: 20px;
}

.ecash_paymethod_type .box > div p {
  font-size: 14px;
  color: #000;
  line-height: 22px;
}

.ecash_paymethod_type .box > div p + ul {
  margin-top: 30px;
}

.ecash_paymethod_type .box > div ul li {
  font-size: 13px;
  color: #595959;
  line-height: 22px;
}

.ecash_paymethod_type .box > div .btn_md {
  padding: 0;
  min-width: 100px;
  width: 100px;
  font-weight: 500;
  font-size: 14px;
}

.ecash_paymethod_type .box > div input[type=text] {
  margin-right: 3px;
  padding: 0 14px;
  width: 400px;
  height: 44px;
  box-sizing: border-box;
  border: 1px solid #d5d5d5;
  border-radius: 6px;
  background-color: #f7f7f7;
}

.ecash_paymethod_type .box > div:only-child {
  margin-top: 0;
  padding-top: 0;
  border-top: 0;
}

.ecash_paymethod_type .box:only-child {
  margin-top: 0;
}

.ecash_paymethod_type .form_rdo + .form_rdo {
  margin-left: 16px;
}

.ecash_payment_header {
  display: flex;
  align-items: center;
  padding-bottom: 21px;
  font-size: 15px;
  color: #595959;
  line-height: 22px;
  border-bottom: 1px solid #eaeaea;
}

.ecash_payment_header b {
  color: #000;
}

.ecash_payment_header span {
  font-weight: 500;
  color: #474c98;
}

.ecash_payment_header button {
  display: inline-block;
  margin-left: auto;
  width: 22px;
  height: 22px;
  background: url(../../common/images/arw_tooltip.png) no-repeat center;
  cursor: pointer;
  transform: rotate(0deg);
  transition: all ease 0.3s;
}

.ecash_payment_header button.close {
  transform: rotate(180deg);
}

.ecash_payment_possession {
  display: flex;
  flex-wrap: wrap;
  align-items: center;
  justify-content: space-between;
  margin-top: 16px;
