  font-weight: 500;
}

.ecash_payment_wrap .tip {
  position: absolute;
  left: 0;
  bottom: -45px;
  display: inline-block;
  padding: 7px 30px 7px 19px;
  font-weight: 500;
  font-size: 12px;
  color: #767676;
  border: 1px solid #b8badf;
  box-sizing: border-box;
  border-radius: 19px;
