  left: 20px;
  top: -4px;
  width: 0;
  height: 0;
  border-left: 4px solid transparent;
  border-right: 4px solid transparent;
  border-bottom: 4px solid #fff;
  z-index: 2;
  content: "";
}

.ecash_notice {
  position: relative;
  margin-top: 70px;
}

.ecash_notice h5 {
  font-size: 19px;
  color: #000;
}

.ecash_notice > div {
  margin-top: 20px;
  padding: 20px;
  border-radius: 16px;
  background-color: #f7f7f7;
}

.ecash_notice > div dl dt {
  font-weight: 600;
  font-size: 14px;
  color: #000;
  line-height: 22px;
}

.ecash_notice > div dl dd {
